      let vm = this
      vm.dark_color_div = false
      vm.show = 'none'
      vm.show1 = 'none'
      vm.show2 = 'none'
      vm.show3 = 'none'
    },
    handleScroll(){
      for(let i=0;i<this.$refs.circle.length;i++){
        let {top,bottom} = this.$refs.circle[i].getBoundingClientRect();
        let height = document.documentElement.clientHeight;
        let x = this.$refs.circle[i];
        if(top<height && bottom > 0){
          this.$refs.button[i].style.transform="scaleX(1)skewX(0deg)";
          this.$refs.circle[i].style.transform="scaleX(1)skewX(0deg)";
          this.$refs.card[i].style.transform="scaleX(1)";
          if(parseFloat(i/2) % 2 == 0 || parseFloat(i/2) == 1){
            this.$refs.circle[i].className = "circle_change"
          }else{
            this.$refs.circle[i].className = "circle_left_change"
            // c.insertRule(".circle.circle::after{content: 'transform:skewX(0deg)scale(1);';}",0);

          }
        }
      }
    },

    show_card(i){
      this.$refs.card[i].style.display = "block";
    }
  }

}

</script>

